<template>
  <div class="top">
    <img
      class="img"
      src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg95.699pic.com%2Fxsj%2F00%2Fqs%2Fl9.jpg%21%2Ffw%2F700%2Fwatermark%2Furl%2FL3hzai93YXRlcl9kZXRhaWwyLnBuZw%2Falign%2Fsoutheast&refer=http%3A%2F%2Fimg95.699pic.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1664691051&t=5ee161b6af2ec88a8e39edf079491863"
      alt=""
    />

    <div class="size">
      很抱歉，您不是本卡单的指定用户，不可以进行激活，请重新确认!
    </div>
    <div class="button">
      <van-button class="but_info" type="info"
        >返回({{ countdown }}s)</van-button
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},

  data() {
    return {
      countdown: 5,
      timer: null, // 定时器名称
    };
  },

  created() {},
  mounted() {
    this.timer = setInterval(() => {
      this.countdownMeth();
    }, 1000);
  },
  methods: {
    countdownMeth() {
      this.countdown--;

      console.log(this.countdown);
      if (this.countdown == 0) {
        this.$router.push({
          path: "/scanIdCard",
        });
      }
    },
  },
  watch: {},
  computed: {},
  destroyed() {
    //清除定时器
    clearInterval(this.timer);
    console.log("beforeDestroy");
  },
};
</script>

<style lang='scss' scoped>
.top {
  display: flex;

  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.img {
  width: 50vw;
}

.size {
  margin-top: 20px;
}

.button {
  height: 30vh;

  display: flex;

  flex-direction: column;
  justify-content: center;
  align-items: center;
  .but_info {
    border-radius: 18px;
  }
}
</style>
